* {
  margin: 0;
  z-index: 80;
}
body{
  overflow-x: hidden;
}
a {
  text-decoration: none;
}

@font-face {
  font-family: LemonMilk;
  src: url('./assets/lemon_milk/LEMONMILK-Regular.otf');
}

@font-face {
  font-family: Rubik;
  src: url('./assets/Rubik/Rubik-VariableFont_wght.ttf');
}

.App {
  display: flex;
  flex-direction: column;
  gap: 100px;
}

#topBar {
  position: sticky;
  top: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 20px 100px;
  background: rgb(0, 0, 0, 0.2);
  z-index: 100;
}

#linksNavBar {
  display: flex;
  flex-direction: row;

  gap: 30px;
}

.linkNavBar {
  font-family: LemonMilk;
  font-size: 30px;
  font-weight: 700;
}

.LemonMilk {
  font-family: LemonMilk;
}

#logoTopBar {
  height: 100%;
  margin-right: 11%;
}

#topBar a {
  color: white;
}

#timerDiv {
  display: flex;
  flex-direction: column;
  gap: 6px;
  align-items: flex-end;
}

#landingBackground {
  height: 100%;
  left: 0;
  object-fit: cover;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -1;
}

#box1 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 80px 18% 100px 18%;
  height: calc(100vh - 320px);
}

#text1 {
  color: white;
  font-size: 120px;
  text-align: center;

}

#text2 {
  color: white;
  font-size: 40px;
  text-align: center;
}

#box2 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#miniBox2 {
  display: flex;
  flex-direction: row;


}

#box3 {

  padding: 0px 18% 100px 18%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}

#box4 {

  padding: 0px 18% 0px 18%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}

#logoBox4 {
  height: 200px;
}

#miniBox4 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 64vw;
  align-items: center;
  background: rgb(0, 255, 255, 0.2);
  padding: 20px 0px;
  border-radius: 16px;
}

#box5 {
  padding: 0px 18% 0px 18%;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}

#box6 {

  padding: 0px 18% 0px 18%;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}

#box7 {

  padding: 80px 18% 100px 18%;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}

#overlay {
  position: fixed ;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .6);
  z-index: 10;
}

.titleBox {
  font-size: 80px;
  font-family: LemonMilk;
  width: 64vw;

  text-align: center;
}

.subTitleBox {
  font-size: 30px;
  background-color: rgb(255, 255, 255, 0.1);
  font-family: Rubik;
  padding: 24px;
  border-radius: 16px;
  width: 64vw;


}

.howToBuyP {
  font-size: 30px;
  background-color: rgba(0, 10, 41, 0.5);
  font-family: Rubik;
  padding: 24px;
  border-radius: 16px;
  color: white;
  width: 85%;
  text-align: justify;
}

.lineHowToBuy {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 64vw;
}

.numberHTB {
  font-size: 100px;
  font-family: LemonMilk;
  color: white;
}

.whiteP {
  color: white;
}

.blackP {
  color: black;
}

.testMiniBox4 {
  width: 20vw;
  color: white;
  border: 5px dashed gold;
  padding: 20px;
  font-size: 30px;
}

#miniBox5 {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  gap: 20px;
}

.underMiniBox5 {
  display: flex;
  flex-direction: column;
  gap: 40px;
  border: 5px dashed gold;
  font-size: 30px;
  padding: 20px;

}

#joinUsDiv {
  display: flex;
  flex-direction: row;
  gap: 60px;
  width: 64vw;
  justify-content: center;
}

.linkJoinUs {
  font-size: 50px;
  font-family: LemonMilk;
  color: rgb(0, 68, 255);
  background-color: rgb(255, 255, 255, 0.7);
  padding: 24px;
  border-radius: 16px;
  width: calc(64vw * 0.25);
  text-align: center;
}

#katVert {
  height: 400px;
}

#tokenBox {
  display: flex;
  flex-direction: row;
  gap: 100px;
  align-items: center;
  background-color: rgba(0, 10, 41, 0.5);
  border-radius: 24px;
  padding: 60px;
  width: calc(36.45vw - 120px);
}

#tokenBox2 {
  display: flex;
  flex-direction: column;
}

.tokenP {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.tokenP1 {
  font-size: 80px;
  font-weight: 700;
  color: white;
}

.tokenP2 {
  font-size: 16px;
  color: white;

}
#hamburger{
  display: none;
}
#menuMobile{
  display: none;
}
@media screen and (max-width: 1500px) {
  .timerP{
    font-size: 10px;
  }
  .subTitleBox {
    font-size: 20px;
  }
  .howToBuyP {
    font-size: 20px;
    padding: 10px;
  }
  .numberHTB{
    font-size: 60px;
  }
  .testMiniBox4 {
    font-size: 16px;
  }

  .titleBox {
    font-size: 50px;
  }

  #text1 {
    font-size: 80px;
  }

  #text2 {
    font-size: 30px;
  }

  #box1 {
    padding: 50px 18%;
    height: calc(100vh - 190px);
  }

  #topBar {
    height: 70px;
    padding: 10px 100px;
  }


  .linkJoinUs{
    font-size: 30px;
  }
}

@media screen and (max-width: 1300px) {
  #text1 {
    font-size: 60px;
  }

  #text2 {
    font-size: 20px;
  }
}

@media screen and (max-width: 1080px) {
  #hamburger{
    display: unset;
    background: transparent;
    border: none;
    height: 90%;
  }
  #hamburgerImg{
    height: 100%;
  }
  .App{
    overflow-x: hidden;
  }
  #topBar{
    padding: 10px;
    position: fixed;
    top: 0;
    width: calc(100vw - 20px);
    display: flex;
    justify-content: space-between;
  }
  #linksNavBar{
    display: none;
  }
  #timerDiv{
    display: none;
  }
  #tokenBox{
    flex-direction: column;
    width: 100%;
    padding: 10px;
  }
  #box1{
    padding: 20px;
    margin-top: 90px;
  }#box5{
    padding: 20px;
  }#box3{
    padding: 20px;
  }#box4{
    padding: 20px;
  }#box5{
    padding: 20px;
  }
  #box6{
    padding: 20px;
  }
  #box7{
    padding: 20px;
  }
  .titleBox{
    font-size: 40px;
    width: calc(100vw - 40px);
    text-align: center;
  }
  #logoBox4{
    display: none;
  }
  #miniBox4{
    flex-direction: column;
  }
  #joinUsDiv{
    flex-direction: column;
    gap: 20px;  }
  .linkJoinUs{
    width: calc(100vw - 40px);
    padding: 24px 0px;
  }
  #joinUsDiv{
    width: 100%;
  }
  .subTitleBox{
    width: calc(100vw - 88px);
  }
  #miniBox4{
    width: calc(100% - 40px);
    padding: 20px 20px;
    gap: 20px;
  }
  .testMiniBox4{
    width: calc(100vw - 120px);
  }
  #tokenBox{
    padding: 30px 10px;
  }
  .lineHowToBuy{
    width: calc(100vw - 40px);
  }
  .howToBuyP{
    font-size: 14px;
    width: 77%;
  }
  #menuMobile{
    background-color: rgb(0, 0, 0, 0.8);
    position: fixed;
    top: 90px;
    flex-direction: column;
    width: 100vw;
    height: calc(100vh - 70px);
    align-items: center;
    gap: 30px;
    padding-top: 30px;
  }
  .closed{
    display: none;
  }
  .open{
    display: flex !important;

  }

}
